.sliderWrapper{
  height: 42px;
  position: relative;
}

.sliderRail h4.ui.header{
  position: absolute;
  margin-top: -14px;
  margin-left: 0%;
  height: 25px;
  z-index: 200;
  /* opacity: 0.8; */
  font-size: 0.8rem;
  /* right: 0; */
  /* text-shadow: 0px 0px 3px #000000;
  color: white; */
  text-shadow: 0px 0px 2px #FFFFFF;
}

.sliderHandle { 
  position: absolute;
  margin-left: -14px;
  margin-top: -1px;
  z-index: 2;
  width: 27px;
  height: 27px;
  cursor: pointer;
  border-radius: 50%;
  box-shadow: 1px 1px 1px 1px rgba(121, 121, 121, 0.2);
  border: 1px solid #a0a0a0;
  background-color: #e9e9e9;
}

.sliderHandle.disabled {
  display: none;
  background-color: #AAAAAA;
}

.sliderRail {
  position: absolute;
  width: 100%;
  height: 25px;
  border-radius: 4px;
  cursor: pointer;
  background-color: #eeeeee;
}

.sliderTick {
  position: absolute;
  margin-top: 0px;
  width: 1px;
  height: 25px;
  background-color: #dbdbdb;
}

.sliderTickNum {
  display: none;
  position: absolute;
  margin-top: 20px;
  font-size: 0.6rem;
  font-family: Arial;
  text-align: center;
  color: #cccccc;
}

.sliderTrack {
  position: absolute;
  height: 25px;
  z-index: 1;
  /* opacity: 0.2; */
  border-radius: 4px;
  cursor: pointer;
}

.sliderTrack.limiter {
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  border-left: 1px solid rgb(180, 180, 180);
  background:   
    /* On "top" */
    repeating-linear-gradient(
      45deg,
      transparent,
      transparent 10px,
      rgba(200, 200, 200, 1) 10px,
      rgba(200, 200, 200, 1) 20px
    ),
    /* on "bottom" */
    linear-gradient(
      to bottom,
      rgba(190, 190, 190, 1),
      rgba(200, 200, 200, 1)
    );
}

.sliderTrack.progress {
  border: 1px solid rgba(12, 93, 243, 0.1);
  background:  
    /* On "top" */
    repeating-linear-gradient(
      45deg,
      transparent,
      transparent 10px,
      rgba(12, 93, 243, 0.1) 10px,
      rgba(12, 93, 243, 0.1) 20px
    ),
    /* on "bottom" */
    linear-gradient(
      to bottom,
      rgba(12, 93, 243, 0.2),
      rgba(12, 93, 243, 0.2)
    );
}

