.ui.image.myPlaceholder{
  position: relative;
  text-align: center;
  background: rgb(240, 240, 240);
  border: 2px solid rgb(235, 235, 235);
  padding: 5px;
  color: rgb(235, 235, 235);
  /* May want to do this if there is risk the container may be narrower than the element inside */
  white-space: nowrap; 
}

.ui.image.myPlaceholder>i.icon{
  position: absolute;
  line-height: normal;
  text-align: center;
  display: table;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  color: rgb(0,0,0, 0.05);
}

.ui.image.myPlaceholder>i:before{
  display: table-cell;
  height: 100%;
  vertical-align: middle;
}

/* This is from https://css-tricks.com/centering-in-the-unknown/ */
.ui.image.myPlaceholder:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
   /*  margin-right: -0.25em;  /* Adjusts for spacing */
}

.ui.image.myPlaceholder>.centered{
  display: inline-block;
  vertical-align: middle;
}

.ui.image.myPlaceholder .ui.header{
  color: #BBBBBB;
  margin: 0;
  padding: 0;
}

.ui.image.myPlaceholder .description{
  color: #999;
  font-size: 0.75rem;
  font-weight: normal;
  font-style: italic;
  margin: 0; padding: 0;
  display: block;
}

.ui.image.myPlaceholder div.json{
  color: black;
  text-align: left;
  margin: 0 20%;
}