#root, .awsappsync, .awsappsync>div {
  height: 100%;
}

.appContainer{
  height: 100%;
  display: flex;
  flex-direction: column;
}

.appContainer>.menu{
  flex: 0 0;
}

.appContainer>.tabContainer{
  flex: 1 1;
  display: flex;
  flex-direction: column;
}
.appContainer>.tabContainer>.menu{
  flex: 0 0 70px;
}
.appContainer>.tabContainer>.ui.segment{
  flex: 1 1;
  overflow: hidden;
  padding: 0;
}
.appContainer>.tabContainer>.ui.segment>.structureList{
  overflow: scroll;
  overflow-x: hidden;
  height: 100%;
}

.homeIcons {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
}

.homepageListItem{
  position: relative;
}


.presentForm .section .ui.header {
  border-bottom: 2px solid #AAAAAA;
}
.presentForm .item .content .header{
  margin-bottom: 0.5rem;
  border-bottom: 1px solid #DDDDDD;
}

.presentForm .item .content .description{
  margin-bottom: 1rem;
}

.ui.addButton.button {
  position: absolute;
  bottom: 2rem;
  right: 2rem;
  font-size: 2.0rem;
  box-shadow: 1px 1px 5px rgba(0,0,0, 0.5)!important;
}

/**
Tighten up the form layout
**/
.ui.segment.BDAFormField {
  padding-left: 0;
  padding-right: 0;
  padding-bottom: 0;
}
.BDAFormField .ui.header{
  font-variant: small-caps;  
}