.ui.image.myPlaceholder{
  position: relative;
  text-align: center;
  background: rgb(240, 240, 240);
  border: 2px solid rgb(235, 235, 235);
  padding: 5px;
  color: rgb(235, 235, 235);
  /* May want to do this if there is risk the container may be narrower than the element inside */
  white-space: nowrap; 
}

.ui.image.myPlaceholder>i.icon{
  position: absolute;
  line-height: normal;
  text-align: center;
  display: table;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  color: rgb(0,0,0, 0.05);
}

.ui.image.myPlaceholder>i:before{
  display: table-cell;
  height: 100%;
  vertical-align: middle;
}

/* This is from https://css-tricks.com/centering-in-the-unknown/ */
.ui.image.myPlaceholder:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
   /*  margin-right: -0.25em;  /* Adjusts for spacing */
}

.ui.image.myPlaceholder>.centered{
  display: inline-block;
  vertical-align: middle;
}

.ui.image.myPlaceholder .ui.header{
  color: #BBBBBB;
  margin: 0;
  padding: 0;
}

.ui.image.myPlaceholder .description{
  color: #999;
  font-size: 0.75rem;
  font-weight: normal;
  font-style: italic;
  margin: 0; padding: 0;
  display: block;
}

.ui.image.myPlaceholder div.json{
  color: black;
  text-align: left;
  margin: 0 20%;
}
.notesVal{
  font-style: italic;
  color: #666;
  word-wrap: normal;
}
#cardListModal .ui.card.active{
  border: 2px solid teal;
  box-shadow: 0 0 5px teal;
}

#cardListModal .ui.card.active .ui.image:before{
  content: ' ';
  position: absolute;
  height: 100%;
  width: 100%;
  display: block;
  background-color: teal;
  z-index: 100;
  opacity: 0.3;
}

#cardListModal .ui.card.active .content .header, 
#cardListModal .ui.card.active .content .description {
  color: teal;
}
.sliderWrapper{
  height: 42px;
  position: relative;
}

.sliderRail h4.ui.header{
  position: absolute;
  margin-top: -14px;
  margin-left: 0%;
  height: 25px;
  z-index: 200;
  /* opacity: 0.8; */
  font-size: 0.8rem;
  /* right: 0; */
  /* text-shadow: 0px 0px 3px #000000;
  color: white; */
  text-shadow: 0px 0px 2px #FFFFFF;
}

.sliderHandle { 
  position: absolute;
  margin-left: -14px;
  margin-top: -1px;
  z-index: 2;
  width: 27px;
  height: 27px;
  cursor: pointer;
  border-radius: 50%;
  box-shadow: 1px 1px 1px 1px rgba(121, 121, 121, 0.2);
  border: 1px solid #a0a0a0;
  background-color: #e9e9e9;
}

.sliderHandle.disabled {
  display: none;
  background-color: #AAAAAA;
}

.sliderRail {
  position: absolute;
  width: 100%;
  height: 25px;
  border-radius: 4px;
  cursor: pointer;
  background-color: #eeeeee;
}

.sliderTick {
  position: absolute;
  margin-top: 0px;
  width: 1px;
  height: 25px;
  background-color: #dbdbdb;
}

.sliderTickNum {
  display: none;
  position: absolute;
  margin-top: 20px;
  font-size: 0.6rem;
  font-family: Arial;
  text-align: center;
  color: #cccccc;
}

.sliderTrack {
  position: absolute;
  height: 25px;
  z-index: 1;
  /* opacity: 0.2; */
  border-radius: 4px;
  cursor: pointer;
}

.sliderTrack.limiter {
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  border-left: 1px solid rgb(180, 180, 180);
  background:   
    /* On "top" */
    -webkit-repeating-linear-gradient(
      45deg,
      transparent,
      transparent 10px,
      rgba(200, 200, 200, 1) 10px,
      rgba(200, 200, 200, 1) 20px
    ),
    /* on "bottom" */
    -webkit-linear-gradient(
      top,
      rgba(190, 190, 190, 1),
      rgba(200, 200, 200, 1)
    );
  background:   
    /* On "top" */
    repeating-linear-gradient(
      45deg,
      transparent,
      transparent 10px,
      rgba(200, 200, 200, 1) 10px,
      rgba(200, 200, 200, 1) 20px
    ),
    /* on "bottom" */
    linear-gradient(
      to bottom,
      rgba(190, 190, 190, 1),
      rgba(200, 200, 200, 1)
    );
}

.sliderTrack.progress {
  border: 1px solid rgba(12, 93, 243, 0.1);
  background:  
    /* On "top" */
    -webkit-repeating-linear-gradient(
      45deg,
      transparent,
      transparent 10px,
      rgba(12, 93, 243, 0.1) 10px,
      rgba(12, 93, 243, 0.1) 20px
    ),
    /* on "bottom" */
    -webkit-linear-gradient(
      top,
      rgba(12, 93, 243, 0.2),
      rgba(12, 93, 243, 0.2)
    );
  background:  
    /* On "top" */
    repeating-linear-gradient(
      45deg,
      transparent,
      transparent 10px,
      rgba(12, 93, 243, 0.1) 10px,
      rgba(12, 93, 243, 0.1) 20px
    ),
    /* on "bottom" */
    linear-gradient(
      to bottom,
      rgba(12, 93, 243, 0.2),
      rgba(12, 93, 243, 0.2)
    );
}

